export class FileMessage {
	filePath : string
	name : string
	extname : string
	constructor(filePath : string, name : string, extname : string) {
		this.filePath = filePath
		this.name = name
		this.extname = extname
	}
}

export class UploadFiles {
	url : string
	filePath : string
	name : string
	header : Object
	formData : Object
	timeout : number

	constructor(url : string, filePath : string, name : string,
		header : Object, formData : Object, timeout : number) {
		this.url = url;
		this.filePath = filePath;
		this.name = name;
		this.header = header
		this.formData = formData
		this.timeout = timeout
	}

	success(res : any) : void {
		console.log("上传成功!")
		uni.showToast({
			title: '上传成功',
			icon: 'success'
		});
	}
	fail(err : any) {
		console.error('上传失败:', err);
		uni.showToast({
			title: '上传失败',
			icon: 'error'
		});
	}

}